<template>
	<div class="detail">
		<BreadcrumbNavigation></BreadcrumbNavigation>
		<template v-if="isShow">
			<div class="orderMsg clearfix">
				<div class="fl tbr">
					<div class="gray-line-block">
						<div class="title">
							订单编号: {{detail.order_sn}}
							<div class="fr status">{{detail.status==2 ? '已出单' : detail.status==3 ? '保障中' : ''}}</div>
						</div>
					</div>
					<div class="tbrMsg">
						<p class="p1 text-hidden">{{detail.plan_name}}</p>
						<p class="p2 text-hidden">投保单位:&nbsp;{{detail.companyName}}</p>
						<p class="p2 text-hidden">保障期间:&nbsp;{{detail.policy_effective_date}} 至 {{detail.policy_expiration_date}}</p>
						<p class="p2 text-hidden">保单号:&nbsp;{{detail.policy_num}}</p>
						<div class="logo">
							<!-- <img :src="detail.companyLogo" v-if="detail.companyLogo"/> -->
						</div>
					</div>
				</div>
				<div class="fr btb">
					<div class="red-line-block">
						<div class="title">被保险人</div>
					</div>
					<div class="btbMsg">
						<p class="p2 text-hidden"><span class="k">姓名:&nbsp;</span>{{detail.member.name}}</p>
						<p class="p2 text-hidden"><span class="k">性别:&nbsp;</span>{{detail.member.genderStr}}</p>
						<p class="p2 text-hidden"><span class="k">证件类型:&nbsp;</span>{{detail.member.idTypeStr}}</p>
						<p class="p2 text-hidden"><span class="k">证件号码:&nbsp;</span>{{detail.member.id_num}}</p>
						<p class="p2 text-hidden"><span class="k">出生日期:&nbsp;</span>{{detail.member.birth}}</p>
					</div>
				</div>
			</div>
			<div class="function">
				<div class="red-line-block">
					<div class="title">保障方案</div>
				</div>
				<div class="content">
					<div class="guarantee-table-box">
						<table class="guarantee-table">
							<thead>
								<tr>
									<th width="160px">保障项目</th>
									<th width="140px">保障金额</th>
									<th width="736px">保障说明</th>
								</tr>
							</thead>
							<tbody>
								<tr v-for='(item,index) in guarantee'>
									<td v-text="item.item"></td>
									<td v-text="item.quota"></td>
									<td v-text='item.description' class="something"></td>
								</tr>
							</tbody>
						</table>
					</div>
				</div>
			</div>
			<div class="settle">
				<div class="blue-line-block">
					<div class="title">理赔流程</div>
				</div>
				<div class="content">
					<div class="content-inner">
						<div v-html='detail.claim_flow' v-if='detail.claim_flow'></div>
					</div>
				</div>
			</div>
			<div class="tipMsg">
				<div class="h">提示信息</div>
				<div class="content" v-html='detail.prompt_info'>
					<!-- <div class="line">1111</div>
					<div class="line">2222</div>
					<div class="line">7777</div> -->
				</div>
			</div>
		</template>
		<template v-else>数据获取中。。。</template>
	</div>
</template>

<script>
	import BreadcrumbNavigation from '../../BaseBreadcrumbNavigation.vue'
	export default {
		components: {
			BreadcrumbNavigation
		},
		data() {
			return {
				detail: {},
				isShow: false
			};
		},
		created(){
			this.$api.personCenter.getGroupInsuranceDetail({
				id: this.$route.params.id
			}).then((data) => {
				console.log(data)
				if(data.code===1){
					this.detail = JSON.parse(JSON.stringify(data)).data
				}
				this.isShow = true
			})
		},
		computed: {
			//保障方案数组
			guarantee(){
				return JSON.parse(this.detail.guarantee ? this.detail.guarantee.details : '[]')
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
.detail{
	width: 1100px;
	height: auto;
	margin: 0 auto;
	font-size: 14px;
	.content{
		padding: 12px 0;
	}
	.orderMsg{
		>*{
			background: #fff;
			margin-bottom: 18px;
		}
		.tbr{
			width: 725px;
			padding-left: 19px;
			.title{
				.status{
					font-size: 14px;
					color: #B6BBBB;
					letter-spacing: 0;
					line-height: 52px;
					margin-right: 19px;
				}
			}
			.tbrMsg{
				position: relative;
				height: auto;
				padding: 25px 0 19px;
				padding-right: 102px;
				.p1{
					font-size: 20px;
					color: #1C1D1D;
					line-height: 30px;
					margin-bottom: 15px; 
					font-weight: 600;
				}
				.p2{
					font-size: 14px;
					color: #1C1D1D;
					line-height: 20px;
					margin: 6px 0;
				}
				.logo{
					position: absolute;
					top: 30px;
					right: 20px;
					width: 82px;
					height: 33px;
					overflow: hidden;
					img{
						float: right;
						height: 100%;
					}
				}
			}
		}
		.btb{
			width: 321px;
			height: 220px;
			padding-left: 19px;
			.btbMsg{
				padding: 12px 0 0;
				.p2{
					font-size: 14px;
					color: #1C1D1D;
					line-height: 20px;
					margin: 6px 0;
					.k{
						display: inline-block;
						width: 64px;
					}
				}
			}
		}
	}
	.function, .settle{
		padding-left: 19px;
		background: #fff;
		margin-bottom: 19px;
		.guarantee-table-box{
			padding: 18px 10px;
			.guarantee-table{
				width: 1039px;
				font-size: 12px;
				border: none;
				border-collapse: collapse;
				table-layout: fixed;
				tbody tr td, thead tr th{
					padding: 12px 8px;
					border: 1px solid #EDEDED;
					box-sizing: border-box;
					text-align: center;
					color: #1C1D1D;
				}
				thead{
					background: #F6F6F6;
				}
				thead tr th{
					line-height: 16px;
				}
				tbody tr td{
					line-height: 18px;
				}
				tbody tr td.something{
					padding-left: 20px;
					padding-right: 20px;
					color: #A5A8A8;
					text-align: left;
				}
			}
		}
		.content-inner{
			padding: 0 0 10px;
		}
	}
	.tipMsg{
		background: #fff;
		margin-bottom: 20px;
		.h{
			height: 44px;
			background: #EDEDED;
			font-size: 16px;
			color: #A5A8A8;
			letter-spacing: 0;
			line-height: 44PX;
			text-align: left;
			padding-left: 19px;
		}
		.content{
			padding: 12px 20px;
			.line{
				font-size: 12px;
				color: #A5A8A8;
				letter-spacing: 0;
				line-height: 28px;
				text-align: left;
			}
		}
	}
}
.text-hidden{
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
.gray-line-block{
	font-size: 14px;
	.title{
		line-height: 52px;
		border-bottom: 1px solid #F1F4F4;
		color: #B6BBBB;
	}
}
.red-line-block{
	border-top: 2px solid #FF703F;
	margin-top: -2px;
	font-size: 16px;
	margin-left: -19px;
	padding-left: 19px;
	.title{
		line-height: 52px;
		border-bottom: 1px solid #F1F4F4;
		color: #FF703F;
	}
}
.blue-line-block{
	border-top: 2px solid #1FD8D2;
	margin-top: -2px;
	font-size: 16px;
	margin-left: -19px;
	padding-left: 19px;
	.title{
		line-height: 52px;
		border-bottom: 1px solid #F1F4F4;
		color: #1FD8D2;
	}
}
</style>